import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSave, faInfoCircle, faBold, faItalic, faQuoteLeft, faLink, faImage, faList, faListOl, faCode, faToggleOn} from '@fortawesome/free-solid-svg-icons'


class Taskbar extends React.Component{
  render(){
    return(
      <div id='taskbar'>
      <ul>
       <li><FontAwesomeIcon id="save" icon={faSave} onClick={this.props.save}/></li>
       <li><FontAwesomeIcon id="info" icon={this.props.icon} onClick={this.props.toggleInfo} /></li>
       <li><FontAwesomeIcon id="bold" onClick={this.props.onClick} icon={faBold}/></li>
       <li><FontAwesomeIcon id="italic" onClick={this.props.onClick} icon={faItalic}/></li>
       <li><FontAwesomeIcon id="quote" onClick={this.props.onClick} icon={faQuoteLeft}/></li>
       <li><FontAwesomeIcon id="link" onClick={this.props.onClick} icon={faLink}/></li>
       <li><FontAwesomeIcon id="picture" onClick={this.props.onClick} icon={faImage}/></li>
       <li><FontAwesomeIcon  onClick={this.props.onClick} icon={faList}/></li>
       <li><FontAwesomeIcon  onClick={this.props.onClick} icon={faListOl}/></li>
       <li><FontAwesomeIcon  onClick={this.props.onClick} icon={faCode}/></li>
       <li><FontAwesomeIcon id="theme-switch" icon={faToggleOn} onClick={this.props.switchTheme}/></li>
       </ul>
      </div>

    )
  }
}

export default Taskbar;
